class Service {
    constructor(QueryHelperService) {
        "ngInject"

        this.QueryHelperService = QueryHelperService;
    }

    /**
     * getList description
     */
    getList(query,currentOption,timePeriod) {
        id: query.id
    };

}

export default Service;


